import React from 'react';
import s from './Question.module.css';
import QuestionImage from './QuestionImage/QuestionImage';

const Question = (props) => {
  const currentData = props.birdsData;
  let rand = props.questionId - 1;

  return (
    <div className={s.quest}>
      <QuestionImage photo = {props.isNext ? currentData[rand].image : 'https://birds-quiz.netlify.com/static/media/bird.06a46938.jpg'} />
      <div className={s.questControl}>
      <span className={s.questSpan}>{props.isNext ? currentData[rand].name : '-----'}</span>
        <hr />
        <audio src={currentData[rand].audio} controls className={s.questAudio}></audio>
      </div>
    </div>
  )
}

export default Question;
