import React from 'react';
import s from './Choice.module.css';
import { NavLink } from 'react-router-dom';

const ChoiceItem = (props) => {
  const coincide = (e) => {
    props.choiceAC(e.target.id);
    props.counterScoreAC();
  }
  
  return (
    <div className={s.choiceList}>
      <NavLink
        onClick={coincide}
        to={`/${props.id}`}
        activeClassName={`${props.isResponse ? s.activeGold : s.activeRed}`}
        id={props.id}
        className={s.listItem}>{props.name}</NavLink>
    </div>
  );
}


const Choice = (props) => {
  
  const options = props.birds.map((b) => <ChoiceItem id={b.id}
                                          choiceAC={props.choiceAC}
                                          key={b.id}
                                          name={b.name}
                                          isResponse={props.isResponse}
                                          counterScoreAC={props.counterScoreAC}
                                          isNext={props.isNext} />);

  return (
    <div className={s.answerOptions}>
        { options }
    </div>
  );
}

export default Choice;
