import React from 'react';
import { connect } from 'react-redux';
import Question from './Question';
// import { questionAC } from '../../redux/question-reducer';

const mapStateToProps = (state) => ({
  birdsData: state.questionBlock.birds,
  isNext: state.questionBlock.isNext,
  choiceId: state.questionBlock.choiceId,
  questionId: state.questionBlock.questionId
})

export default connect(mapStateToProps, {})(Question);
