import birdsData from "../api/api";

const NEXT_LEVEL_AC = 'NEXT_LEVEL_AC';
const CHOICE_AC = 'CHOICE_AC';
const COUNTER_SCORE = 'COUNTER_SCORE';

const initialState = {
  counter: 0,
  birds: birdsData[0],
  isNext: false,
  choiceId: null,
  questionId: Math.floor(Math.random() * 5) + 1,
  isResponse: null,
  score: 0,
  counterScore: 6
}

const questionReducer = (state = initialState, action) => {
  switch(action.type) {
    case NEXT_LEVEL_AC:
      if(state.counter < 5) {
        return {
          ...state,
          counter: state.counter + 1,
          birds: birdsData[state.counter + 1],
          isNext: false,
          choiceId: null,
          questionId: Math.floor(Math.random() * 5) + 1,
          isResponse: null,
          score: state.score,
          counterScore: 6
        }
      } else if(state.counter === 5) {
        return {
          ...state,
          counter: state.counter + 1,
        }
      } else {
        return {
          ...state,
          counter: 0,
          birds: birdsData[0],
          isNext: false,
          choiceId: null,
          questionId: Math.floor(Math.random() * 5) + 1,
          isResponse: null,
          score: 0,
          counterScore: 6
        }
      }
    case CHOICE_AC:
      return {
        ...state,
        choiceId: +action.id,
        isResponse: state.questionId === +action.id || state.isResponse ? true : false,
        isNext: state.questionId === +action.id || state.isResponse ? true : false
      }
    case COUNTER_SCORE:
      return {
        ...state,
        counterScore: (!state.isNext && state.counterScore > 1 && state.counterScore - 1) || (!state.isNext && state.counterScore),
        score: (state.isNext && state.score + state.counterScore) || state.score
      }
    default:
      return state
  }
}

export const nextLevelAC = () => ({type: NEXT_LEVEL_AC});
export const choiceAC = (id) => ({type: CHOICE_AC, id});
export const counterScoreAC = () => ({type: COUNTER_SCORE});

export default questionReducer;
