import React from 'react';
import s from './Next.module.css';
import { NavLink } from 'react-router-dom';

const Next = (props) => {
  const nextLevel = () => {
    if(props.isNext) {
      props.nextLevelAC();
      props.choiceAC(null);
    }
  }
  return <NavLink to={props.isNext ? '/' : `${props.location.pathname}`} className={s.nextBth} onClick={nextLevel}>Next Level </NavLink>
}

export default Next;
