import React from 'react';
import Choice from './Choice';
import { connect } from 'react-redux';
import { choiceAC, counterScoreAC } from '../../redux/question-reducer';

const mapStateToProps = state => ({
  birds: state.questionBlock.birds,
  isResponse: state.questionBlock.isResponse,
  isNext: state.questionBlock.isNext
})

export default connect(mapStateToProps, {choiceAC, counterScoreAC})(Choice);
